.footer {
	background-color: var(--container-color);
	border-top: 1px solid var(--border-color);
}
.footer__conteiner {
	padding: 2rem 0 4rem;
}
.footer__title,
.footer__link {
	color: var(--title-color);
}
.footer__title {
	text-align: center;
	margin-bottom: var(--mb-2);
}
.footer__link:hover {
	color: var(--title-color-dark);
}

.footer__list {
	display: flex;
	justify-content: center;
	column-gap: 1.5rem;
	margin-bottom: var(--mb-2);
}

.footer__social {
	display: flex;
	justify-content: center;
	column-gap: 1.25rem;
}
.footer__social-link {
	background-color: var(--title-color);
	color: var(--container-color);
	font-size: 1.1125rem;
	padding: 0.4rem;
	border-radius: 0.5rem;
	display: flex;
	justify-content: center;
	align-items: center;
}
.footer__social-link i::before {
	position: relative;
	top: -4px;
}

.footer__social-link i {
	height: 18px;
}

.footer__social-link:hover {
	background-color: var(--title-color-dark);
}
.footer__copy {
	display: block;
	margin-top: 2.5rem;
	color: var(--title-color);
	text-align: center;
	font-size: var(--smaller-font-size);
}

@media screen and (max-width: 992px) {
	.footer__social-link {
		padding: 0.25rem;
		border-radius: 0.25rem;
		font-size: 1rem;
	}
	.footer__social-link i::before {
		top: -3.5px;
		left: 0.5px;
	}
}
